      display: block;
    }
    .w-input {
      display: flex;
    }
    .shrink-post {
      display: inline-block !important;
    }
  }

  &.shrinked-post {
    animation: shrink-post forwards 0.4s ease;
    position: relative;
    top: 0;
  }

  .likes-comments {
    font-size: 1.1em;
    width: 100%;
    height: 50px;
    display: grid;
    grid-template-columns: 80px 80px auto;
    justify-items: start;
    align-items: center;

    .topic {
      width: fit-content;
      height: fit-content;
      color: var(--text);
      font-weight: bold;
      border: 2px solid var(--accent);
      border-radius: 40px;
      padding: 6px 12px;
      font-size: 0.6em;
      justify-self: end;
      cursor: pointer;
      &:hover {
        background-color: var(--accent);
        color: white;
      }
    }

    button {
      font-size: 0.8em;
      font-weight: bold;
      background-color: transparent;
      color: var(--text);
      padding: 10px;
      height: 100%;
