<template>
  <div>
    <div class="comment">
      <span>
        <i class="admin-badge" v-if="comment.author[3] === 'Admin'"></i>
        {{ comment.author[0] }}
      </span>
      <p>{{ comment.text }}</p>
      <div class="tools">
        <button>
          <input
            type="checkbox"
            :id="comment._id + '-comment-like-button'"
            @change="editLikes($event)"
            :checked="comment.likes.includes(thisUser.name)"
          />
          <label :for="comment._id + '-comment-like-button'"></label>
          {{ comment.likes.length }}
        </button>
        <button
          v-if="comment.onPost"
          class="fas fa-reply"
          @click="reply()"
        ></button>
        <button
          class="fas fa-trash"
          v-if="thisUser.isAdmin || comment.author[0] === thisUser.name"
          @click="deleteComment()"
        ></button>
      </div>
    </div>
    <div class="replies">
      <slot><!-- replies will go here --></slot>
    </div>
    <transition name="opacity">
      <w-popup ref="popup" />
    </transition>
  </div>
</template>

<script>
import axios from "axios";
import WPopup from "./WPopup.vue";
export default {
  components: { WPopup },
  name: "WComment",
  props: ["commentObject", "parentComment"],
  data() {
    return {
      comment: JSON.parse(this.commentObject),
    };
  },
  computed: {
    thisUser() {
      return this.$store.state.user;
    },
    lang() {
      return this.$store.state.lang;
    },
    appVocubulary() {
      return this.$store.state.appVocubulary[this.lang].pages.Posts;
    },
  },
  methods: {
    reply() {
      this.$emit("reply-to", [
        this.comment._id,
        `${this.comment.author[0]} : ${this.comment.text}`,
      ]);
    },
    async deleteComment() {
      const confirmationResult = await this.$refs.popup.show({
        message: this.appVocubulary.deleteComment,
        confirmText: "DELETE",
        cancelText: "CANCEL",
      });
      if (confirmationResult) {
        if (this.comment.onPost) {
          // means that the comment is not reply
          await axios.delete(
            `${this.$store.state.backend_link}comments/${this.comment._id}`
          );
        } else {
          await axios.delete(
            `${this.$store.state.backend_link}comments/reply/${this.parentComment}/${this.comment._id}`
          );
        }
        this.$emit("refresh");
      }
    },
    async editLikes(e) {
      let likesCheck = e.currentTarget.parentElement.childNodes[0],
        likesText = e.currentTarget.parentElement.childNodes[2];
      const operation = likesCheck.checked ? "plus" : "minus";
      if (operation === "plus") {
        likesText.textContent = Number.parseInt(likesText.textContent) + 1;
      } else {
        likesText.textContent = Number.parseInt(likesText.textContent) - 1;
      }
      if (this.comment.onPost) {
        await axios.put(
          `${this.$store.state.backend_link}comments/${this.comment._id}`,
          {
            name: this.thisUser.name,
            operation: operation,
          }
        );
      } else {
        await axios.put(
          `${this.$store.state.backend_link}comments/reply/${this.parentComment}/${this.comment._id}`,
          {
            name: this.thisUser.name,
            operation: operation,
          }
        );
      }
      this.$emit("refresh");
      // const num = this.comment.onPost ? 1 : 0;
      // if (operation === "plus") {
      //   likesText.textContent = comment.data.likes.length + 1;
      // } else {
      //   likesText.textContent = comment.data.likes.length - 1;
      // }
    },
  },
};
</script>

