    display: flex;
    flex-flow: column nowrap;
    justify-content: space-evenly;
    align-items: center;
    font-size: 0.9em;
    button {
      margin: 0;
      padding: 4px;
      background: transparent;
      color: var(--text);
      font-size: 0.9em;
      label {
        margin: 0 2px;
